<template>
<div>
    <div class="banner" @click="handleShowGallary">
        <img class="banner-img" :src="bannerImg" />
        <div class="banner-info">
            <div class="banner-title">{{this.sightName}}</div>
            <div class="banner-number">
                <span class="iconfont banner-icon">&#xe679;</span>
                {{this.bannerImgs.lenght}}
            </div>
        </div>
    </div>

    <fade>
    <common-gallary :imgs="bannerImgs" v-show="showGallary"  @close="handleCloseGallary"></common-gallary>
    </fade>
</div>
    
</template>

<script>
import CommonGallary from 'common/gallary/Gallary'
import Fade from 'common/fade/Fade'
export default {
    name: 'DetailBanner',
    props:{
        sightName:String,
        bannerImg:String,
        bannerImgs:Array
    },
    data(){
        return {
            showGallary:false
        }
    },
    components:{
        CommonGallary,
        Fade
    },
    methods:{
        handleShowGallary(){
            this.showGallary = true
        },
        handleCloseGallary(){
            this.showGallary = false
        }
    }
}
</script>

<style lang="stylus" scoped>
.banner{
    position relative
    overflow hidden
    height 0
    padding-bottom 55%
}
.banner-img{
    width 100%
}
.banner-info{
    position absolute
    left 0
    right 0
    bottom 0
    line-height .6rem
    display flex
    color #fff
    //下层渐变阴影
    background-image linear-gradient(top,rgba(0, 0, 0, 0),rgba(0, 0, 0, 0.8))  
}
.banner-title{
    flex 1
    font-size .38rem
    padding 0 .2rem .2rem .2rem
}
.banner-number{
    padding .05rem .3rem
    line-height .32rem
    height .32rem
    border-radius .26rem
    background rgba(0, 0, 0, .5)
    font-size .24rem
    position absolute
    top -.45rem
    left .2rem
}
.banner-icon{
    font-size .22rem
}
</style>





