<template>
<div>
    <div class="search">
        <input class="search-input" type="text" placeholder="输入城市名或拼音" v-model="keyword"/>
        <div class="search-content" ref="search" v-show="keyword">
          <ul>
              <li class="search-item border-bottom"
               v-for="item of list" :key="item.id" @click="handleCityClick(item.name)"
               >
               {{item.name}}
               </li>
              <li class="search-item border-bottom" v-show="noData">没有对应的城市名</li>
          </ul>
        </div>
    </div>
</div>
</template>

<script>
import BScroll from 'better-scroll'
import {mapMutations} from 'vuex'
export default {
    name:'CitySearch',
    props :{
        cities :Object
    },
    data () {
        return {
            keyword:'',
            timer:null,
            list:[]
        }
    },
    methods:{
         handleCityClick (city) {
           // this.$store.dispatch('changeCity',city)
           // this.$store.commit('changeCity',city)
           this.changeCity(city)
           this.$router.push('/')
        },
        ...mapMutations(['changeCity'])
    },
    computed:{
        noData (){
            return !this.list.length
        }
    },
    watch :{
        keyword () {
            if(this.timer){
                clearTimeout(this.timer)
            }
            if(!this.keyword){
                this.list = []
                return
            }
            this.timer = setTimeout(() => {
                const result = []
                for(let i in this.cities){
                    this.cities[i].forEach((value) => {
                        if(value.spell.indexOf(this.keyword)>-1||value.name.indexOf(this.keyword)>-1){
                            result.push(value)
                        }
                    })
                }
                this.list = result
            },100)
        }
    },
    mounted () {
        this.scroll = new BScroll(this.$refs.search)
    }
}
</script>

<style lang="stylus" scoped>
@import '~style/varibles.styl';
.search{
    height .72rem
    padding 0 .12rem
    background $bgColor
}
.search-input{
    height .6rem
    line-height .6rem
    width 100%
    text-align center
    border-radius .06rem
    color #666
    padding 0 .1rem
    box-sizing border-box
}
.search-content{
    overflow hidden
    position absolute
    top 1.58rem
    left 0
    right 0
    bottom 0
    background #eee
    z-index 1
}
.search-item{
    line-height .62rem
    padding-left .2rem
    color #666
    background #fff
}
</style>



