//vuex
import Vue from 'vue'
import Vuex from 'vuex'
import state from './state'
import mutations from './mutations'

Vue.use(Vuex)




export default new Vuex.Store({
    //组件共用数据
    state,
    //接受来自组件的数据
    /*actions:{
        //ctx可以拿到commit方法
        changeCity(ctx,city){
            ctx.commit('changeCity',city)
        }
    },*/
    //改变组件共用数据
    mutations,
  })


