<template>
    <div class="container" @click="handleGallaryClick">
        <div class="wrapper">
            <swiper :options="swiperOption">
            <!-- slides -->
            <swiper-slide v-for="(item,index) in imgs" :key="index">
                <img class="swiper-img" :src="item" />
            </swiper-slide>

            <!-- Optional controls -->
            <div class="swiper-pagination"  slot="pagination"></div>
            </swiper>
        </div>
    </div>
</template>

<script>
export default {
    name:'CommonGallary',
    props:{
        imgs:{
            type:Array,
            default (){
                return []
            }
        }
    },
    data (){
        return {
            swiperOption:{
                pagination:'.swiper-pagination',
                paginationType: 'fraction',
                //解决进入画廊时轮播图无法正常滑动的情况
                //监听该元素或dom结构发生变化时重新刷新一次
                observeParents:true,
                observer:true
            }
        }
    },
    methods:{
        handleGallaryClick (){
            this.$emit('close')
        }
    }
}
</script>

<style lang="stylus" scoped>
.container >>> .swiper-container{
    overflow inherit
}
.container{
    position fixed
    top 0
    left 0
    right 0
    bottom 0
    background #000
    z-index 99
    display flex
    flex-direction column
    justify-content center
}
.wrapper{
    width 100%
    height 0
    padding-bottom 80%
}
.swiper-img{
    width 100%
}
.swiper-pagination{
    color #fff
    bottom -3rem
}
</style>


