<template>
    <div class="list" ref="wrapper">
        <div>
        <div class="area">
            <div class="title border-topbottom">当前城市</div>
            <div class="btn-list">
                <div class="btn-wrapper">
                    <div class="btn">{{this.currentyCity}}</div>
                </div>
            </div>
        </div>
        <div class="area">
            <div class="title border-topbottom">热门城市</div>
            <div class="btn-list">
                <div class="btn-wrapper" v-for="item of hot" :key="item.id" @click="handleCityClick(item.name)">
                    <div class="btn">{{item.name}}</div>
                </div>
            </div>
        </div>
        <!--首字母城市列表-->
        <div class="area" v-for="(item,key) of cities" :key="key" :ref="key">
            <div class="title border-topbottom">{{key}}</div>
            <div class="item-list" v-for="InnerItem of item" :key="InnerItem.id" @click="handleCityClick(InnerItem.name)">
                <div class="item border-topbottom">{{InnerItem.name}}</div>
            </div>
        </div>
        </div>
    </div>
</template>

<script>
import BScroll from 'better-scroll'
import {mapState,mapMutations} from 'vuex'
export default {
    name:'CityList',
    props:{
        hot:Array,
        cities:Object,
        letter:String
    },
    methods:{
        handleCityClick (city) {
           // this.$store.dispatch('changeCity',city)
           // this.$store.commit('changeCity',city)
            this.changeCity(city)
            this.$router.push('/')
        },
        ...mapMutations(['changeCity'])
    },
    computed:{
        ...mapState({
            currentyCity:'city'
        })
    },
    mounted(){
        
        this.scroll = new BScroll(this.$refs.wrapper,{
            click:true,
            tap:true
        })
    },
    watch:{
        letter(){
            if(this.letter){
                const ele = this.$refs[this.letter][0]
                this.scroll.scrollToElement(ele)
            }
        }
    }
}
</script>

<style lang="stylus" scoped>
@import '~style/varibles.styl';
.border-topbottom{
    &:before{
        border-color #ccc
    }
    &:after{
        border-color #ccc
    }
}
.list{
    position absolute
    top 1.58rem
    left 0
    right 0
    bottom 0
    overflow hidden
}
.title{
    padding-left .2rem
    line-height .6rem
    background #eee
    font-size .24rem
}
.btn-list{
    padding .1rem .6rem .1rem .1rem
    overflow hidden
}
.btn-wrapper{
    float left 
    width 33.33%
}
.btn{
    text-align center
    margin .1rem .1rem
    border .02rem solid #ccc
    padding .1rem 0
    border-radius .06rem
}
.item{
    line-height .76rem
    padding-left .2rem
}
</style>


