<template>
    <div class="header">
      <div class="header-left">
          <div class="iconfont back-icon">&#xe624;</div>
      </div>
      <div class="header-input">
          <span class="iconfont">&#xe632;</span>
          输出城市/景点/游玩主题
      </div>
      <router-link to="/city">
        <div class="header-right">
        {{this.city}}
        <span class="iconfont down-icon">&#xe64a;</span>
        </div>
      </router-link>
      
    </div>
        
</template>

<script>
import {mapState} from 'vuex'
export default {
    name:'HomeHeader',
    computed:{
        ...mapState(['city'])
    }
}
</script>

<style lang="stylus" scoped>
@import '~style/varibles.styl';
 .header{
     display flex;
     line-height $headerHeight;
     background $bgColor
     color #fff
 }
 .header-left{
     width .64rem;
     float left
 }
 .back-icon{
     font-size .4rem
     text-align center
 }
 .header-input{
     flex 1
     background-color #fff
     border-radius .1rem
     line-height .64rem
     color #ccc
     height .64rem
     margin-top .1rem
     margin-left .2rem
     padding-left .2rem
 }
 .header-right{
     min-width 1.04rem
     padding 0 0.1rem
     float right
     text-align center
     color #fff
 }
 .down-icon{
     font-size .24rem
     margin-left -.04rem
 }
 
  
</style>





