<template>
    <ul class="list">
      <li class="item" v-for="item of letters" :key="item" @click="handleCkick"
       @touchstart.prevent="TouchStart" @touchmove="TouchMove" @touchend="TouchEnd" :ref="item"
      >{{item}}</li>
    </ul>
</template>

<script>
export default {
    name:'CityAlphabet',
    props:{
        cities:Object
    },
    data(){
        return {
            Touchstart:false,
            startY:0,
            timer:null
        }
    },
    computed:{
        letters(){
            const letters = []
            for(let i in this.cities){         //????????????????????????????????
                letters.push(i)
            }
            return letters
        }
    },
    updated(){
        this.startY = this.$refs['A'][0].offsetTop    //获取A距顶部的距离
    },
    methods:{
        handleCkick(e){
         this.$emit('change',e.target.innerText)
        },
        TouchStart(){
         this.Touchstart = true
        },
        TouchMove(e){
         if(this.Touchstart){
           if(this.timer){
               clearTimeout(this.timer)
           }
           this.timer = setTimeout(() => {                  //函数节流，降低TouchMove函数的执行频率，提高性能
                const touchY = e.touches[0].clientY - 77    
                const index = Math.floor((touchY - this.startY) / 20)   
                if(index >= 0 && index < this.letters.length){
                    this.$emit('change',this.letters[index])
                }
           },16)
           
         }
        },
        TouchEnd(){
         this.Touchstart = false
        }
    }
}
</script>

<style lang="stylus" scoped>
@import '~style/varibles.styl';
.list{
    display flex
    flex-direction column
    justify-content center
    text-align center
    position absolute
    top 1.58rem
    right 0
    width .4rem
    line-height .44rem
    color $bgColor
    bottom 0
}
</style>


