<template>
    <div>
        <div class="title">热销推荐</div>
        <ul>
            <router-link tag="li" class="item" v-for="item of list" :key="item.id" :to="'/detail/'+item.id">
                <img class="item-img" :src="item.imgUrl" />
                <div class="item-info">
                    <p class="item-title">{{item.title}}</p>
                    <p class="item-desc">{{item.desc}}</p>
                    <button class="item-btn">查看详情</button>
                    <span class="local">{{item.local}}</span>
                </div>
            </router-link>
        </ul>
    </div>
</template>
<script>
export default {
    name : 'HomeRecommend',
    props:{
        list:Array
    }
}
</script>

<style lang="stylus" scoped>
@import '~style/mixins.styl';
.title{
    margin-top .2rem
    line-height .8rem
    background #eee
    text-indent .2rem
}
.item{
    overflow hidden
    display flex
    height 2.1rem
    margin-top .3rem
    border-bottom 1px solid #eee
}
.item-img{
    width 1.9rem
    height 1.9rem
    padding .1rem
}
.item-info{
    flex 1
    padding .1rem
    min-width 0
}
.item-title{
    margin-top .1rem
    line-height .54rem
    font-size .32rem
    ellipsis()
}
.item-desc{
    line-height .4rem
    color #999
    font-size .26rem
    margin-top .1rem
    margin-bottom .06rem
    ellipsis()
}
.item-btn{
    background #ff9300
    padding 0 .2rem
    border-radius .06rem
    color #ffffff
    line-height .44rem
    margin-top .06rem
}
.local{
    float right
    font-size .24rem
    margin-top .2rem
}
</style>




